.header {
  margin-bottom: 46px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);

  &_logo {
    padding: 0;
  }

  &_image {
    display: block;
    width: 40px;
    height: 40px;
  }
}
