.card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: $width-card;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  transition: 300ms;

  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin-bottom: 40px;
    align-self: center;
  }

  &__title {
    margin-bottom: 4px;
    @include styleFont(12px, 500, 18px);
    color: $main-color;
  }

  &__code {
    margin-bottom: 16px;
    @include styleFont(10px, 400, 14px);
    color: $secondary-color;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__reviews-count {
    @include styleFont(10px, 400, 14px);
    color: $main-color;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;

    &-text {
      @include styleFont(12px, 400, 18px);
      color: $secondary-color;
    }

    &-sum {
      @include styleFont(16px, 700, 18px);
      color: $main-color;
    }
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 166px;
    @include styleFont(14px, 700, 16px);
    text-transform: uppercase;
    text-decoration: none;
    background-color: $hover-color;
    color: $white-color;
    border: 1px solid $hover-color;
    border-radius: 5px;

    &:hover {
      background-color: $white-color;
      color: $hover-color;
    }
  }

  &:hover {
    transform: scale(1.2);
  }

  &:hover &__title {
    color: #34568b;
  }
}
